import { Component, NgZone, OnInit, Renderer2 } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { OrderDetailsBarComponent } from '../../shared/components/order-details-bar/order-details-bar.component';
import { HfButtonComponent, HfCardComponent, HfCheckboxComponent, HfInputComponent, HfModelComponent, HfRadioComponent, HfToastService, inputMsgType } from '@monorepo/ui';
import { FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { AppService, StoreService, maxLengthRestriction } from '@monorepo/utils';
import { CookieHelper } from '../../shared/services/cookie.helper';
import { environment } from '../../../environments/environment';
import { ServiceType, PaymentMode } from '../../shared/models/enums';
import { Router } from '@angular/router';
import { CommonModule } from '@angular/common';
import { OrderInvoiceComponent } from '../../shared/components/order-invoice/order-invoice.component';
import { URLS } from '../../shared/utils/urls';
import { loadScript } from "@paypal/paypal-js";

@Component({
  selector: 'app-payment',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, OrderDetailsBarComponent, HfCardComponent, HfButtonComponent, HfRadioComponent, HfInputComponent, HfCheckboxComponent, OrderInvoiceComponent, HfModelComponent],
  templateUrl: './payment.component.html',
  styleUrl: './payment.component.scss'
})

export class PaymentComponent implements OnInit {

  orderParams: any;
  paymentType: FormControl = new FormControl('');
  showAddressCard: boolean = false;
  paymentMode = PaymentMode;
  cardPaymentInit: boolean = true;
  orderData: object;
  invoiceData: object;
  showTermsModal: boolean = false;
  companyDetails: any;
  cardLoaded: boolean = false;
  paypal: any;
  paymentButtonLabel: string = 'Pay & Confirm';
  isPaymentButtonDisabled: boolean = false;
  paymentTypeLabel: string = 'Credit Card';
  isPaypalLoaded: boolean = false;
  isPayPalAvailable: boolean = true;

  billingForm = new FormGroup({
    isBillingAdressSame: new FormControl(true),
    billingAdress: new FormControl('', [Validators.required, Validators.minLength(3)]),
    billingCity: new FormControl('', [Validators.required]),
    billingState: new FormControl('', [Validators.required]),
    billingZip: new FormControl('', [Validators.required, Validators.minLength(5), maxLengthRestriction(5)]),
  });

  paymentForm = new FormGroup({
    cardHolderName: new FormControl('', [Validators.required]),
  });

  get isBillingAdressSame() {
    return this.billingForm.get('isBillingAdressSame') as FormControl;
  }

  get billingAdress() {
    return this.billingForm.get('billingAdress') as FormControl;
  }

  get billingCity() {
    return this.billingForm.get('billingCity') as FormControl;
  }

  get billingState() {
    return this.billingForm.get('billingState') as FormControl;
  }

  get billingZip() {
    return this.billingForm.get('billingZip') as FormControl;
  }

  get cardHolderName() {
    return this.paymentForm.get('cardHolderName') as FormControl;
  }

  cardNumberIM: string = 'Invalid Card Number';
  cardNumberMT: inputMsgType = '';
  cardExpireyIM: string = 'Invalid Expiry Date';
  cardExpireyMT: inputMsgType = '';
  cardCvvIM: string = 'Invalid CVV';
  cardCvvMT: inputMsgType = '';
  cardHolderNameIM: string = 'Invalid Card Holder Name';
  cardHolderNameMT: inputMsgType = '';
  billingAdressIM: string = '';
  billingAdressMT: inputMsgType = '';
  billingCityIM: string = '';
  billingCityMT: inputMsgType = '';
  billingStateIM: string = '';
  billingStateMT: inputMsgType = '';
  billingZipIM: string = '';
  billingZipMT: inputMsgType = '';
  orderSummaryData: any;

  constructor(
    private http: HttpClient,
    private storeService: StoreService,
    private cookieHelper: CookieHelper,
    private renderer: Renderer2,
    private router: Router,
    private toastService: HfToastService,
    public appService: AppService,
    private zone: NgZone
  ) { }

  ngOnInit() {
    if (this.appService.isBrowserRendering) {
      this.selectPaymentType();
    }
    this.openBillingAdress();
  }

  selectPaymentType() {
    this.storeService.getValueFromStore('orderParams', (orderParams) => {
      this.orderParams = orderParams;
      if (this.orderParams['mode'] == this.paymentMode.CASH) {
        this.paymentTypeLabel = 'Cash On Delivery';
        this.paymentType.setValue('cash');
      } else if (this.orderParams['mode'] == this.paymentMode.PAYPAL) {
        this.paymentTypeLabel = 'PayPal';
        this.paymentType.setValue('paypal');
      } else {
        this.paymentTypeLabel = 'Credit Card';
        this.paymentType.setValue('card');
      }
      this.callSummaryApi();
    });
  }

  callSummaryApi() {
    let body = {
      isZipChanged: false,
      isMultipleFips: false,
      isDealAvailable: true,
      isFlashSale: false,
      isFlatDeliveryFee: this.orderParams['isFlatFee'],
      flatDeliveryFee: this.orderParams['flatFee'],
      isFuelAdditive: this.orderParams['fuel-additive'],
      serviceName: '',
      companyId: this.orderParams['companyId'],
      companyName: this.orderParams['name'],
      price: this.orderParams['price'],
      customerType: 0,
      deliveryDay: this.orderParams['dayId'],
      isTermsAccepted: false,
      isUpsale: false,
      orderType: ServiceType.OilDelivery,
      payWithCash: this.orderParams['mode'] == this.paymentMode.CASH ? true : false,
      serviceId: -1,
      tankSize: 275,
      userId: parseInt(this.cookieHelper.getUserID()),
      zipCode: this.orderParams['zipCode'],
      city: this.orderParams['city'],
      county: this.orderParams['county'],
      fipsCode: this.orderParams['fips'],
      state: this.orderParams['state'],
      amount: parseInt(this.orderParams['quantity']),
      token: '',
      heatFleetFlashSaleId: this.orderParams['heatFleetFlashSaleId'],
      deliveryId: parseInt(this.orderParams['addressId']),
      SupportUserName: null,
      CallCompanyId: null,
      ReferredBy: null,
      isCompanyWebsiteUrl: null
    }

    const url = `${environment.api_url}customer/order/summary`;
    this.http.post(url, body).subscribe({
      next: (response) => {
        this.orderSummaryData = response;
        this.orderData = response;
        this.invoiceData = response['oilOrderParameters']['placedOrder'];
        if (this.orderParams['mode'] == this.paymentMode.CREDIT) {
          this.addCollectionJS('https://secure.networkmerchants.com/token/Collect.js', () => {
            this.configureCollectionJs();
            this.cardPaymentInit = false;
          });
        } else if (this.orderParams['mode'] == this.paymentMode.PAYPAL) {
          this.payPalInit();
        }
      },
      error: (error) => {
        console.log(error);
      }
    });
  }

  async payPalInit() {
    try {
      this.paypal = await loadScript({ clientId: environment.paypal_client_id, currency: 'USD', components: ["buttons", "marks", "messages"] });
      this.zone.run(() => {
        this.isPaypalLoaded = true;
      })
      this.paypal.Buttons(
        {
          fundingSource: this.paypal.FUNDING.PAYLATER,
          createOrder: (data: any, actions: any) => {
            return actions.order.create({
              purchase_units: [
                {
                  amount: {
                    value: this.orderSummaryData['price'].toFixed(2),
                  },
                },
              ],
            });
          },
          onApprove: (data: any, actions: any) => {
            return actions.order.capture().then((details: any) => {
              this.verifyPayment(details, 'paypal');
            });
          },
          onError: (err: any) => {
            console.log(err);
            this.toastService.toastMessage.next({ title: `Payment Failed`, msg: 'Please try another time', type: 'error' });
          }
        }
      ).render("#paypal-button").catch((error: any) => {
        this.isPayPalAvailable = false;
        console.error("failed to render the PayPal Buttons", error);
      });
    } catch (error) {
      console.error("failed to load the PayPal JS SDK script", error);
    }
  }

  addCollectionJS(url: string, callback: () => void): void {
    const script = this.renderer.createElement('script');
    script.src = url;
    script.setAttribute('data-variant', 'inline');
    script.setAttribute('data-tokenization-key', '24x5CN-Y34yun-9cy3V7-t5MAZ2');
    this.renderer.appendChild(document.head, script);
    script.onload = callback;
  }

  configureCollectionJs() {
    (window as any).CollectJS.configure({
      variant: 'inline',
      price: this.orderSummaryData['price'].toFixed(3),
      styleSniffer: true,
      currency: 'USD',
      country: 'US',
      customCss: {
        'border-style': 'none',
        'background-color': 'transparent',
        'font-size': '18px',
        'font-weight': '500'
      },
      callback: (response: any) => {
        this.verifyPayment(response, 'nmi');
      },
      fields: {
        ccnumber: {
          selector: '#ccnumber'
        },
        ccexp: {
          selector: '#ccexp'
        },
        cvv: {
          selector: '#cvv'
        }
      },
      validationCallback: (field: any, status: any, message: any) => {
        if (field == 'ccnumber') {
          this.validateCCNumber(status, message);
        }
        if (field == 'ccexp') {
          this.validateCCExp(status, message);
        }
        if (field == 'cvv') {
          this.validateCVV(status, message);
        }
      },
      fieldsAvailableCallback: () => {
        this.cardLoaded = true;
      }
    });
  }

  validateCCNumber(status: boolean, message: string) {
    this.zone.run(() => {
      if (!status) {
        this.cardNumberMT = 'error';
        this.cardNumberIM = message;
        this.paymentButtonLabel = 'Pay & Confirm';
        this.isPaymentButtonDisabled = false;
      } else {
        this.cardNumberMT = '';
        this.cardNumberIM = '';
      }
    });
  }

  validateCCExp(status: boolean, message: string) {
    this.zone.run(() => {
      if (!status) {
        this.cardExpireyMT = 'error';
        this.cardExpireyIM = message;
        this.paymentButtonLabel = 'Pay & Confirm';
        this.isPaymentButtonDisabled = false;
      } else {
        this.cardExpireyMT = '';
        this.cardExpireyIM = '';
      }
    });
  }

  validateCVV(status: boolean, message: string) {
    this.zone.run(() => {
      if (!status) {
        this.cardCvvMT = 'error';
        this.cardCvvIM = message;
        this.paymentButtonLabel = 'Pay & Confirm';
        this.isPaymentButtonDisabled = false;
      } else {
        this.cardCvvMT = '';
        this.cardCvvIM = '';
      }
    });
  }

  openBillingAdress() {
    this.isBillingAdressSame.valueChanges.subscribe((value) => {
      this.showAddressCard = !value;
    });
  }

  validatePayment() {
    if (!this.isBillingAdressSame.value) {
      if (this.billingForm.invalid) {
        if (this.billingAdress.invalid) {
          this.billingAdressMT = "error";
          if (this.billingAdress?.errors['required']) {
            this.billingAdressIM = "Address is required";
          } else if (this.billingAdress?.errors['minlength']) {
            this.billingAdressIM = "Address is too short";
          }
          const validBillingAdress = this.billingAdress.statusChanges.subscribe((status) => {
            if (status === 'VALID') {
              this.billingAdressIM = '';
              this.billingAdressMT = '';
              validBillingAdress.unsubscribe();
            }
          });
        }
        if (this.billingCity.invalid) {
          this.billingCityMT = "error";
          if (this.billingCity?.errors['required']) {
            this.billingCityIM = "City is required";
          }
          const validBillingCity = this.billingCity.statusChanges.subscribe((status) => {
            if (status === 'VALID') {
              this.billingCityMT = '';
              validBillingCity.unsubscribe();
            }
          });
        }
        if (this.billingState.invalid) {
          this.billingStateMT = "error";
          if (this.billingState?.errors['required']) {
            this.billingStateIM = "State is required";
          }
          const validBillingState = this.billingState.statusChanges.subscribe((status) => {
            if (status === 'VALID') {
              this.billingStateMT = '';
              validBillingState.unsubscribe();
            }
          });
        }
        if (this.billingZip.invalid) {
          this.billingZipMT = "error";
          if (this.billingZip?.errors['required']) {
            this.billingZipIM = "Zip is required";
          } else if (this.billingZip?.errors['minlength']) {
            this.billingZipIM = "Zip is too short";
          }
          const validBillingZip = this.billingZip.statusChanges.subscribe((status) => {
            if (status === 'VALID') {
              this.billingZipMT = '';
              validBillingZip.unsubscribe();
            }
          });
        }
        return;
      }
    }
    if (this.paymentType.value === "card") {
      if (this.paymentForm.invalid) {
        if (this.cardHolderName.invalid) {
          this.cardHolderNameMT = "error";
          if (this.cardHolderName?.errors['required']) {
            this.cardHolderNameIM = "Card holder name is required";
          }
          const validCardHolderName = this.cardHolderName.statusChanges.subscribe((status) => {
            if (status === 'VALID') {
              this.cardHolderNameMT = '';
              this.cardHolderNameIM = '';
              validCardHolderName.unsubscribe();
            }
          });
        }
        return;
      }
    }
    this.makePayment();
  }

  makePayment() {
    this.zone.run(() => {
      if (this.cardNumberMT === '' && this.cardExpireyMT === '' && this.cardCvvMT === '' && this.cardHolderNameMT === '') {
        this.paymentButtonLabel = 'Processing...';
        this.isPaymentButtonDisabled = true;
      }
    });
    if (this.orderParams['mode'] != this.paymentMode.CASH && this.cardHolderNameMT === '') {
      (window as any).CollectJS.startPaymentRequest();
    } else {
      this.verifyPayment({ token: '' }, 'cash');
    }
  }

  verifyPayment(res: any, paymentType: 'nmi' | 'paypal' | 'cash') {
    this.zone.run(() => {
      this.paymentButtonLabel = 'Processing...';
      this.isPaymentButtonDisabled = true;
    });

    let body = {
      isZipChanged: false,
      isMultipleFips: false,
      isDealAvailable: true,
      isFlashSale: false,
      isFlatDeliveryFee: this.orderParams['isFlatFee'],
      flatDeliveryFee: this.orderParams['flatFee'],
      isFuelAdditive: this.orderParams['fuel-additive'],
      serviceName: '',
      companyId: this.orderParams['companyId'],
      companyName: this.orderParams['name'],
      price: this.orderParams['calculatedPrice'],
      customerType: 0,
      deliveryDay: this.orderParams['dayId'],
      isTermsAccepted: false,
      isUpsale: false,
      orderType: ServiceType.OilDelivery,
      payWithCash: false,
      serviceId: -1,
      tankSize: 275,
      userId: parseInt(this.cookieHelper.getUserID()),
      zipCode: this.orderParams['zipCode'],
      city: this.orderParams['city'],
      county: this.orderParams['county'],
      fipsCode: this.orderParams['fips'],
      state: this.orderParams['state'],
      amount: parseInt(this.orderParams['quantity']),
      token: null,
      heatFleetFlashSaleId: this.orderParams['heatFleetFlashSaleId'],
      deliveryId: parseInt(this.orderParams['addressId']),
      BillingAddressSameAsDeliveryAddress: this.isBillingAdressSame.value,
      BillingStreet1: this.orderParams['street1'],
      BillingCity: this.orderParams['city'],
      BillingState: this.orderParams['state'],
      BillingZIP: this.orderParams['zipCode'],
      BillingCounty: this.orderParams['county'],
      BillingFIPS: this.orderParams['fips'],
      SupportUserName: null,
      CallCompanyId: null,
      ReferredBy: null,
      isCompanyWebsiteUrl: null,
    }

    if (!this.isBillingAdressSame.value) {
      body.BillingStreet1 = this.billingAdress.value;
      body.BillingCity = this.billingCity.value;
      body.BillingState = this.billingState.value;
      body.BillingZIP = this.billingZip.value;
    }

    if (paymentType === 'cash') {
      body.payWithCash = true;
    } else if (paymentType === 'paypal') {
      body.token = res['id'];
      body['paymentType'] = "Paypal";
      body['paymentSuccess'] = res.purchase_units[0].payments.captures[0].status;
      body['paymentCaptureId'] = res.purchase_units[0].payments.captures[0].id;
      body['payPalResponse'] = JSON.stringify(res);
    } else if (paymentType === 'nmi') {
      body.token = res['token'];
    }

    const url = `${environment.api_url}customer/order/place`;
    this.http.post(url, body).subscribe({
      next: (response) => {
        this.zone.run(() => {
          if (response['isError']) {
            this.toastService.toastMessage.next({ title: `Payment Failed`, msg: `${response['errorMessage']} Please try other credit card or debit card`, type: 'error' });
          } else {
            this.orderParams['orderId'] = response['id'];
            this.storeService.addValueToStore('orderParams', this.orderParams);
            this.toastService.toastMessage.next({ title: `Payment Success`, msg: 'Order placed successfully', type: 'success' });
            this.router.navigate([URLS.DELIVERY_DETAILS]);
          }
          this.paymentButtonLabel = 'Pay & Confirm';
          this.isPaymentButtonDisabled = false;
        });
      },
      error: (error) => {
        this.zone.run(() => {
          this.paymentButtonLabel = 'Pay & Confirm';
          this.isPaymentButtonDisabled = false;
          this.toastService.toastMessage.next({ title: `Payment Failed`, msg: 'Something went wrong, Please contact support', type: 'error' });
          console.log(error);
        });
      }
    });
  }

  openTermsModal() {
    if (!this.showTermsModal) {
      this.showTermsModal = true;
      if (!this.companyDetails) {
        const url = `${environment.api_url}customer/offer/get-companydetails?companyId=${this.orderParams['companyId']}`;
        this.http.get(url).subscribe({
          next: (response) => {
            this.companyDetails = response;
          }
        })
      }
    }
  }

  closeTermsModal(isClosed: boolean) {
    if (isClosed) {
      this.showTermsModal = false;
    }
  }

  selectPaypalPaymentType() {
    this.paymentType.setValue('paypal');
  }
}
